import { createContext, useContext, useMemo } from 'react';

import { FormContextData, FormContextType, FormProps } from './types';

export const FormContext = createContext<FormContextData>({});

export const useFormContext = (params: Omit<FormProps, 'dataTestId'>): FormContextType => {
  const {
    labelWidth,
    labelAside,
    size
  } = params;
  const { labelWidthForm, labelAsideForm, sizeForm } = useContext(FormContext);

  return useMemo(() => ({
    LABEL_WIDTH: labelWidthForm ?? labelWidth,
    LABEL_ASIDE: labelAsideForm ?? labelAside,
    SIZE: sizeForm ?? size
  }), [
    labelWidth,
    labelAside,
    size,
    labelWidthForm,
    labelAsideForm,
    sizeForm
  ]);
};
